.imagen_principal {
    margin: 4rem 0;
    display: flex;
    flex-direction: row;
    align-items: center;

    @include tablet{
        flex-direction:column;
        width:100%;
        margin: 0;
    }
    img {
        max-width: 35rem;
        border-radius:50%;
    }

    h2{
        font-size: 3rem;
        font-weight: 400;
        position: relative;
        padding: 0px 0px 0 20px;
        line-height: 50px;
        margin:1.5rem 0;
        @include tablet{
            font-size: 2rem;
        position: relative;
        line-height: 40px;
        margin:1.5rem 0;
        } 
    }

    .right{
        @include telefono{
            width: 50%;
            float: initial;
            margin:0 auto;
            display:table;
        }
    }
    
} 
.texto{
    width: 70%;
    /* float: left; */
    margin: 0 auto;
    display: table;
    padding: 5rem 0;
    @include tablet{
        width:100%;
    }
    p{
        font-size: 1.6rem;
        line-height: 25px;
        text-align: justify; 
        
        span{color:$cafe;font-weight: $bold;}
    }

    ul{
      margin-bottom:3rem;
      list-style:none;
        li{
            margin:2rem 0;
            
        }
       
    }  
    .titulos{
        img{
        @include tablet{
            display:none;
        }
    }
    }
}
.tachado{text-decoration: line-through;}
.destacado{color:$azul!important;padding:3rem 0;}
.titulos {
    display: flex;
        justify-content: flex-start;
        align-items: first baseline;
        flex-direction: column;
        padding: 30px 0;
    img {
        max-width: 10rem;
        float: left;
        margin: 1rem 3rem;
    }
    h3{width: 100%;
    font-size:2rem;
font-weight: $bold;
color:$amarillo;}
}
.imagen_cuerpo {
    @include tablet{
        flex-direction: column;
    }
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 3rem;
    
    img {
        max-width: 60%;
    }

}

#Anteriores .imagen_cuerpo{
    padding:4rem 0;
}

  
#Contacto{
    .texto{
        p{
            line-height: 30px;
            text-align: inherit;
        }
    }
}